*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
#outcont{
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(255,255,255,0.5);
}
.backg{
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    transition: opacity 3s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    opacity: 0;
}
.vis{
    opacity:1;
}
