    background: linear-gradient(135deg, #f6e58d 0%, #ffbe76 20%, #fab1a0 50%, #f8c291 80%);
  }
  
  #backg2 {
    background: linear-gradient(45deg, #b8e994 0%, #7ed6df 20%, #e056fd 50%, #95afc0 80%);
  }
  
  #backg3 {
    background: linear-gradient(90deg, #b8e994 0%, #55efc4 20%, #74b9ff 50%, #a29bfe 80%);
  }
  
  #backg4 {
    background: linear-gradient(180deg, #f9ca24 0%, #f0932b 20%, #eb4d4b 50%, #6ab04c 80%);
