

.nslide_inner {
    width: 100%;
    height: 70%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    position:relative;
    top:15%;
}

.nslide_inner>div {
    height: 98%;
}

.cover_images,
.genre {
    box-shadow: 1vmin 1vmin 5px black;
}

.cover_images {
    width: 45vh;

    transition: all 0.5s ease;
}

.nslide_inner:hover .cover_images,
.genre {
    box-shadow: 0 0 5px black;
    transform: translateY(1vmin) translateX(1vmin);
    border-radius: 1vmin;
}

a {
    color: black;
    text-decoration: none;
}

.genre,
.summary {
    padding: 20px;
}

.genre {
    width: 10vw;
    border: 1px solid green;
    transition: all 0.5s ease;
}

.genre ul {
    list-style-type: none;
}

.genre li {
    display: block;
    color: rgb(100, 100, 100);
    padding: 5px 0;
    font-size: 1vw;
    font-weight: bolder;
}

.summary {
    width: 35vw;
    flex-grow: 1;
    line-height: 1.6;
    color: white;
    font-size: 3vmin;
    text-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    font-family: 'Franklin Gothic Medium';
    text-overflow: clip;
}
.titles{
    color:rgb(90,90,90);
    position: absolute;
    right: 1vw;
    top: -2vw;
    font-size: 2vw;
    font-weight: 700;
}