#trailer_cont{
    width: 80vw;
    margin-left: 10vw;
    height: 0;
    overflow: hidden;
    transition:height 1s ease;
}
#trailer{
    width: 80vw;
    height: 40vw;
}

#no_trailer{
    margin-top: 50px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: 500;
    font-size: 5vh;
    line-height: 7vh;
    text-align: center;
    color: white;
    text-shadow: 0.1vh 0.1vh 0.1vh black;
}

#movie_single{
    width: 80%;
    height: 420px;
    margin-top: 50px;
    margin-left: 10%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    transition: height 1s ease;
    margin-bottom: 50px;
}
#mid_cov_img{
width: 300px;
height: 420px;
}
#movie_contbox{
    width: 0;
    height: 100%;
    transition: width 0.5s linear;
    position: relative;
    color:rgb(70,70,70);
    overflow: hidden;
}
#movie_contbox_inner{
    width:calc(80vw - 300px);
    height: 100%;
}

#title_back{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    font-size: 4em;
    font-weight: bolder;
    color:rgba(255,255,255,0.2);
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

#title_front{
    position: absolute;
    width: 100%;
    height: 100%;
    padding-left: 5%;
    z-index: 1;
    font-weight: bolder;
    overflow: auto;
}



#title{
    font-size: 2em;
    width: 100%;
}

#genres{
    margin-top: 30px;
}

.movie_genres{
    display: inline;
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
    margin-right:16px;
    color:white
}

#summary_ghost{
    display: none;
}

#summary{
    width: 100%;
    margin-top: 30px;
}