.imgslide_cont {
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: rgb(37,37,37);
    opacity: 0;
}

.imgslide_midcont {
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    position: relative;
    top: 0;
}

.imgslide_target {
    position: relative;
    top: 0;
    left: 0;
    height: 100%;
}

.imgslide_target img{
    height: 100%;
    opacity: 0.5;
}