.ls_cont{
    text-align:center;
    height: 10vh;
    line-height: 10vh;
    font-size: 5vh;
    font-weight: bold;
    color:rgb(51, 131, 96);
}

.ls_outer{
    width: 2vh;
    height: 5vh;
    transition:all 0.1s ease;
    background-color: white;
    display: inline-block;
    margin:0 0.5vh;
}

#ls_outer1{
    animation: jewel 4s linear infinite;
    animation-delay: 0.1s;
}
#ls_outer2{
    animation: jewel 4s linear infinite;
    animation-delay: 0.2s;
}
#ls_outer3{
    animation: jewel 4s linear infinite;
    animation-delay: 0.3s;
}
.ls_inner{
    width: 1vh;
    height: 3vh;
    margin-top: 1vh;
    margin-left: 1vh;
    transition:all 0.1s ease;
    background-color: green;
}
.tranformTarget{
    border: 3px solid black;
    background-color: linear-gradient(135deg, #f6e58d 0%, #ffbe76 20%, #fab1a0 50%, #f8c291 80%);
}

@keyframes jewel {

    0%{
        background-color: white;
        border: 0px solid white;
    }
    40%{
        background-color: linear-gradient(45deg, #ffb8c6 0%, #ffcec2 20%, #ffecd9 50%, #b8f2e5 80%);
        border: 0.2vh solid linear-gradient(135deg, #a6c0fe 0%, #667eea 35%, #764ba2 70%, #9b51e0 100%);
    }
    80%{
        background-color: linear-gradient(135deg, #a6c0fe 0%, #667eea 35%, #764ba2 70%, #9b51e0 100%);
        border: 0.2vh solid linear-gradient(45deg, #ffb8c6 0%, #ffcec2 20%, #ffecd9 50%, #b8f2e5 80%);
    }

    100%{
        background-color: white;
        border: 0px solid white;
    }
    
}
